/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

//set layouts and content area widths for subpages

body:not(.home):not(.page-template-about):not(.post-type-archive):not(.tax-product_type):not(.single-product):not(.single-adventure) .site-content {
  display: flex;
  margin: 0 auto;
  width: 1120px;
}

body:not(.home):not(.page-template-about):not(.single-adventure):not(.archive):not(.tax-product_type):not(.search):not(.search-results):not(.error404):not(.page-template-default) .content-area,
body:not(.home):not(.page-template-about):not(.single-adventure):not(.post-type-archive-products):not(.search):not(.search-results):not(.error404):not(.page-template-default) .widget-area {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

body:not(.home):not(.page-template-about):not(.single-adventure):not(.archive):not(.tax-product_type):not(.single-products) .content-area {
  width: 74%;
}

body:not(.home):not(.page-template-about) .site-content {
  padding-top: 50px;
}

/* Front page */

body.home {
  .hero-image-banner {
    height: 100vh;
    background: url("../../images/inhabitent-logo-full.svg") no-repeat center, linear-gradient(rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url("../../images/home-hero.jpg") no-repeat center top;
    background-size: 275px, cover, 100%;
    margin-bottom: 2.5rem;
  }

  & h2 {
    font-size: 2.4rem;
    font-weight: 100;
    margin-bottom: 3.75rem;
    text-align: center;
  }

  /********* Product area ******/

  & .shopstuff-container {
    margin: 0 auto 4rem;
    width: 78%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;

    & h2 {
      width: 100%;
      text-align: center;
      margin-bottom: 3rem;
    }

    & .shop-stuff-item {
      width: 23%;
      border: $basic__border;
      border-top: 5px solid #e1e1e1;
      padding: 0.75rem 0.75rem 1.75rem;
      text-align: center;

      &:hover {
        border: 1px solid $color__orange-brand;
        border-top: 5px solid $color__orange-brand;
      }

      & .product-icon-image {
        width: 70px;
        margin: 0 auto;
      }

      & .button-link {
        @include green-button;

        color: white;
        text-decoration: none;
        padding: 0.8rem $base__space-unit;
      }
    }
  }

  /********* Blog area ****/

  .front-page-posts-wrapper {
    margin: 3rem auto;
    width: 78%;
    display: flex;
    flex-flow: row wrap;
    align-items: baseline;

    & article:nth-child(2) {
      border-right: 0;
    }

    & article:nth-child(3) {
      border-right: 0;
      border-left: 1rem solid white;
    }

    & article {
      width: 33.333%;
      border-right: 1rem solid white;

      & .entry-header {
        border: $basic__border;
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-start;

        img {
          max-height: 248px;
          border-bottom: 4px solid #E1E1E1;
          order: 0;
        }
      }

      & .entry-title {
        margin-top: 0.5rem;
        margin-bottom: 1rem;
        margin-left: 0.75rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        order: 2;

        & a {
          font-size: 1.3rem;
          line-height: 1.75rem;
          text-align: left;
          text-decoration: none;
          color: $color__green-brand;
          display: flex;

          &:hover {
            color: $color__purple-brand;
          }
        }
      }

      & .entry-meta {
        margin-top: 0.75rem;
        margin-left: 0.75rem;
        order: 1;
        font-size: 0.8rem;
      }

      & .read-more-button {
        margin-left: 0.75rem;
        margin-bottom: 0.75rem;
        order: 4;
      }
    }
  }
}

/********* Adventures area ****/

.adventures-container {
  width: 78%;
  height: auto;
  margin: 2rem auto 3rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;

  & .more-adventures-button {
    @include green-button;

    width: auto;
    margin: $base__space-unit 0;
    padding: 0.55rem $base__space-unit 2.25rem;
    font-size: 1.15rem;
    font-weight: 100;
    color: white;
  }

  & .adventures-block-left {
    border-right: 0.75rem solid white;
    width: 49.5%;
  }

  & .adventures-block-right {
    width: 50.5%;
    display: flex;
    flex-flow: row wrap;
    border-left: 0.75rem solid white;
  }

  & .adventures-block-left, & .adventures-block-right {
    height: 540px;
    box-sizing: border-box;

    & .adventure {
      box-sizing: border-box;
      overflow: hidden;
      text-align: left;
      padding: 0 $base__space-unit;
      postion: absolute;

      & .adventure-title {
        font-family: 'merriweather', Arial, serif;
        font-weight: 300;
        color: white;
        text-transform: none;
        letter-spacing: 1px;

        &:hover {
          color: $color__yelow-brand;
          cursor: pointer;
        }
      }

      & .adventure-button {
        @include transparent-button(#FFF);

        padding: 0.8rem $base__space-unit;
        text-decoration: none;
        position: relative;
      }
    }
  }

  & .adventures-block-left .adventure {
    height: 100%;
    width: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../images/adventure-photos/canoe-girl.jpg") no-repeat center bottom;
    background-size: 170%;
    background-position: 50% 80%;

    & .adventure-title {
      font-size: 3rem;
      line-height: 4rem;
    }

    & .adventure-button {
      bottom: 12px;
    }
  }

  & .adventures-block-right .adventure:nth-child(1) {
    height: 50%;
    width: 100%;
    border-bottom: 1.25rem solid white;
    background: linear-gradient(rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../images/adventure-photos/beach-bonfire.jpg") no-repeat center center;
    background-size: 125%;
    background-position: 50% 70%;

    & .adventure-title {
      font-size: 2.45rem;
      line-height: 3.15rem;
      transform: translate(-1%, 15%);
    }

    & .adventure-button {
      top: 10px;
    }
  }

  & .adventures-block-right .adventure:nth-child(2) {
    height: 50%;
    width: 50%;
    border-right: 1.25rem solid white;
    background: linear-gradient(rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../images/adventure-photos/mountain-hikers.jpg") no-repeat center;
    background-size: 245%;
    background-position: 52% 75%;

    & .adventure-title {
      font-size: 1.7rem;
      line-height: 2rem;
      transform: translateY(23%);
    }

    & .adventure-button {
      top: 20px;
    }
  }

  & .adventures-block-right .adventure:nth-child(3) {
    height: 50%;
    width: 50%;
    background: linear-gradient(rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.4) 100%), url("../../images/adventure-photos/night-sky.jpg") no-repeat center;
    background-size: 230%;
    background-position: 49% 75%;

    & .adventure-title {
      font-size: 1.7rem;
      line-height: 2rem;
      transform: translateY(37%);
    }

    & .adventure-button {
      top: 18px;
    }
  }
}

// Journal Page

.blog {
  & .entry-content {
    width: 760px;
  }

  & .entry-header {
    position: relative;

    & img {
      width: 760px;
    }

    & .entry-title {
      width: 550px;
      height: auto;
      font-size: 2rem;
      line-height: 2.25rem;
      color: white;
      background-color: $color__green-brand;
      padding: 0.75rem 1.5rem;
      position: absolute;
      top: 10px;
      left: 0;

      & a:hover {
        color: $color__yelow-brand;
        cursor: pointer;
      }

      & a {
        text-decoration: none;
        font-size: 2rem;
        color: white;
      }
    }

    & .entry-meta {
      height: auto;
      font-family: 'novocento', Arial, serif;
      font-size: 0.9rem;
      color: white;
      background-color: $color__green-brand;
      padding: 0.4rem $base__space-unit;
      text-transform: uppercase;
      text-align: center;
      position: absolute;
      bottom: 8px;
      right: 60px;
    }
  }
}

.single-post {
  & .entry-content {
    width: 760px;
  }

  & .entry-header {
    position: relative;

    & img {
      width: 760px;
    }

    & .entry-title {
      width: 550px;
      height: auto;
      font-size: 2rem;
      line-height: 2.25rem;
      color: white;
      background-color: $color__green-brand;
      padding: 0.75rem 1.5rem;
      position: absolute;
      top: 10px;
      left: 0;

      & a:hover {
        color: $color__yelow-brand;
        cursor: pointer;
      }

      & a {
        text-decoration: none;
        font-size: 2rem;
        color: white;
      }
    }

    & .entry-meta {
      height: auto;
      font-family: 'novocento', Arial, serif;
      font-size: 0.9rem;
      color: white;
      background-color: $color__green-brand;
      padding: 0.4rem $base__space-unit;
      text-transform: uppercase;
      text-align: center;
      position: absolute;
      bottom: 8px;
      right: 60px;

      & a {
        text-decoration: none;
        color: white;

        &:hover {
          color: $color__yelow-brand;
        }
      }
    }
  }
}

// About page

body.page-template-about {
  & .content-area .hero-image-banner {
    height: 100vh;
    width: auto;
    background-size: cover;
    background-position: center bottom;
    position: relative;

    & .about-header {
      font-size: 3rem;
      color: white;
      position: absolute;
      top: 40%;
      left: 43.5%;
    }
  }

  & .site-main {
    width: 800px;
    margin: 0 auto;
    padding-top: 0.75rem;
    padding-bottom: 3rem;
    line-height: 1.8rem;

    & .entry-header:nth-child(2) {
      margin-top: 2rem;
    }

    & .entry-header {
      width: 300px;
      margin: 3rem auto 2rem;
      font-size: 2rem;
      color: $color__purple-brand;
      position: relative;
      left: 6%;
    }
  }
}

// Blog single-page view

.single-post .post-navigation {
  height: 0;
  display: none;
}

.single-post .entry-footer {
  letter-spacing: 0.5px;

  & .cat-links {
    margin-right: $base__space-unit;
  }

  & .social-buttons button {
    width: auto;
    margin: 2rem 0 2rem;
    padding: 0.25rem $base__space-unit;
    border: 1px solid $color__border-abbr;
    color: black;
    background-color: white;
    text-transform: uppercase;

    &:hover {
      color: white;
      background-color: $color__green-brand;
      border: 1px solid $color__green-brand;
    }
  }

  & span {
    font-family: 'novocento', Arial, serif;
    font-size: 0.95rem;
    text-transform: uppercase;

    & a {
      text-decoration: none;
      color: $color__green-brand;

      &:hover {
        color: $color__purple-brand;
      }
    }
  }
}

/////****** Blog Comments Area

.comments-area {
  width: 93%;

  & h3 {
    margin-top: 1rem;
    font-size: 1.75rem;
  }

  & h2 {
    font-size: 2rem;
    color: $color__purple-brand;
  }

  .comment-list {
    margin: 0;
    padding: 0;
    border-bottom: $basic__border;
    list-style-type: none;

    & li:first-child {
      border-top: none;
    }

    & li {
      padding-top: $base__space-unit;
      border-top: $basic__border;

      & .children {
        border-top: $basic__border;
        list-style-type: none;

        & .comment-author-avatar img {
          min-width: 50px;
        }
      }

      .comment-body {
        width: 92%;
        display: flex;
        padding-bottom: $base__space-unit;

        & .comment-author-avatar {
          margin-right: $base__space-unit;

          & img {
            border-radius: 50px;
          }
        }

        & .comment-content .comment-author cite {
          text-transform: uppercase;
          font-weight: bold;
          font-style: normal;
        }

        & .comment-metadata {
          margin-top: 0.3rem;
          color: #808080;

          & a {
            text-decoration: none;
            color: $color__green-brand;
          }
        }

        & .reply a {
          text-decoration: none;
          color: $color__green-brand;
        }
      }
    }
  }

  .comment-form-comment {
    margin-top: 2rem;
    font-family: 'novocento', Arial, serif;
    text-transform: uppercase;

    & label {
      font-size: 1.1rem;
      letter-spacing: 0.5px;
    }

    & .form-submit {
      margin-bottom: 1rem;
    }
  }
}

// Blog category archive page

.category {
  .content-area {
    padding-top: $base__space-unit;

    .site-main {
      width: 760px;

      .page-header {
        width: 640px;
        margin-top: 0.75rem;
        margin-bottom: 3rem;
        border-bottom: $basic__border;

        .page-title {
          font-size: 2.4rem;
        }
      }

      .entry-header {
        position: relative;

        .entry-title {
          width: 550px;
          height: auto;
          font-size: 2rem;
          line-height: 2.25rem;
          color: #fff;
          background-color: #248a83;
          padding: .75rem 1.5rem;
          position: absolute;
          top: 10px;
          left: 0;

          & a {
            text-decoration: none;
            color: white;

            &:hover {
              color: $color__yelow-brand;
            }
          }
        }
      }
    }
  }
}

// Shop archive page

.post-type-archive-products {
  .content-area {
    padding-top: 0;
    width: 100%;

    .page-header {
      margin: 2.75rem auto 2rem;

      h1.page-title {
        width: 400px;
        margin: 0 auto;
        font-size: 2.4rem;
        display: flex;
        justify-content: center;
      }

      .shop-terms-container {
        width: 25%;
        margin: $base__space-unit auto 2.5rem;
        font-family: 'novocento', Arial, serif;
        font-size: 1.2rem;
        font-weight: 100;
        display: flex;
        justify-content: space-around;
        text-transform: uppercase;

        & a {
          text-decoration: none;
          color: $color__green-brand;
        }
      }
    }
  }
}

.shop-content-wrapper {
  width: 80%;
  margin: 0 auto;
  padding-top: 3rem;
  padding-bottom: 4.5rem;
  border-top: $dashed__border;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-content: center;

  & article {
    height: auto;
    width: 23%;
    border: $basic__border;

    & .entry-header {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;
      align-items: center;

      & .product-image-wrapper {
        width: 100%;
        overflow: auto;
        display: flex;
        flex-flow: column nowrap;
        align-items: center;

        & img {
          height: 260px;
          width: auto;
          max-width: none;
          border-bottom: 5px solid #e1e1e1;
          position: relative;
          top: 50%;
          left: 0.5%;
        }
      }

      & .product-entry-meta {
        height: 55px;
        width: 100%;
        margin-top: -0.25rem;
        padding: 0 1rem;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: baseline;
        position: relative;

        & .ellipses {
          position: absolute;
          z-index: -1;
          margin-top: 0.5rem;
          overflow: hidden;
          width: 75%;
          padding: 0 0.3rem;
        }

        & p {
          padding: 0 0.15rem;
          font-size: 0.9rem;
          font-weight: 100;
          color: $color__text-main;
          background-color: white;
        }

        & .entry-title {
          font-family: 'merriweather', Arial, serif;
          font-size: 0.95rem;
          font-weight: 100;
          letter-spacing: 0px;
          color: black;
          background-color: white;
          padding: 0 0.15rem;
          display: inline-block;
          text-transform: none;

          & a {
            font-weight: 100;
            color: $color__text-main;
            height: 40px;
            font-family: 'merriweather', Arial, serif;
            font-size: 1.2rem;
            text-transform: none;
            text-decoration: none;
          }
        }
      }
    }
  }
}

// Single product page

body.single-products {
  .site-content {
    width: 75%;
  }

  .content-area {
    width: 100%;

    & .entry-header {
      width: 100%;
      display: flex;

      & .product-image-wrapper {
        min-width: 48%;
      }

      & .entry-meta {
        padding-left: 3rem;
        padding-bottom: 4rem;

        & .entry-title {
          width: 50%;
          margin-top: 0;
          color: $color__orange-brand;
          display: inline;
        }

        & p.price {
          margin-top: 0.75rem;
          font-size: 1.5rem;
          font-family: novocento,Arial,serif;
          letter-spacing: 0.5px;
        }

        & .entry-content {
          width: auto;
          margin-bottom: $base__space-unit;
          font-size: 1rem;
          line-height: 1.9rem;
        }
      }

      & .social-buttons button {
        width: auto;
        padding: .25rem 1.5rem;
        border: 1px solid #666;
        color: #000;
        background-color: #fff;
        text-transform: uppercase;

        &:hover {
          color: white;
          background-color: $color__green-brand;
          border: 1px solid $color__green-brand;
        }
      }
    }
  }
}

// Product type archive page

body.tax-product_type {
  & .site-content {
    width: 100%;

    & .content-area {
      width: 100%;

      & .page-header {
        text-align: center;
        padding-top: 2rem;
        padding-bottom: 2rem;
      }

      & h1 {
        font-size: 2.5rem;
        margin-top: 0.5rem;
        margin-bottom: 1.75rem;
      }
    }
  }
}

// Search results page

.search, .search-results {
  .site-main {
    padding-top: 3rem;
    padding-bottom: 3rem;
    width: 93%;

    .page-header {
      border-bottom: $basic__border;

      .page-title {
        margin-top: 0;
        margin-bottom: 1rem;
        font-size: 2.35rem;
      }
    }

    & article {
      padding-top: 1rem;
      margin-bottom: 0;
      line-height: 1.75rem;

      &:last-of-type {
        padding-bottom: 2.5rem;
        border-bottom: $basic__border;
      }

      & .read-more-button {
        margin-bottom: 1.75rem;
      }

      & .entry-title {
        font-size: 2.15rem;

        & a {
          color: $color__green-brand;
          text-decoration: none;
        }
      }
    }

    .search-pagination {
      padding-top: $base__space-unit;
      padding-bottom: $base__space-unit;

      .current {
        background-color: #808080;

        &:hover {
          background-color: #808080;
          cursor: default;
        }
      }

      & a, & span.current {
        @include green-button;

        padding: 0.55rem 0.75rem;
        margin-right: 0.25rem;
        font-size: 0.9rem;
        text-decoration: none;
        color: white;

        &:hover {
          background-color: $color__purple-brand;
        }

        &:active, &:focus {
          border: none;
        }
      }
    }
  }
}

// 404 Error page

.error404 {
  font-size: 16px;

  & .content-area {
    padding-bottom: 2.5rem;

    & .site-main {
      padding-top: 1rem;

      & .page-title {
        font-size: 2.4em;
      }

      & .widgettitle {
        color: $color__purple-brand;
      }

      & .search-form {
        margin-bottom: 2.5rem;

        & fieldset {
          border: none;
          padding: 0;

          & .search-submit {
            display: none;
            height: 0;
          }

          & .search-field {
            background-color: #F4F4F4;
            height: 30px;
            text-transform: none;
          }
        }
      }

      & .widget {
        margin-bottom: 3rem;

        & ul {
          margin: 0;
          padding: 0;
          list-style-type: none;
          line-height: 1.75rem;

          & a {
            text-decoration: none;
            color: $color__green-brand;
          }
        }
      }

      & .widget_archive p {
        margin-bottom: 0.75rem;
      }
    }
  }
}

// Find us page 

.page-id-52 {
  .content-area {
    padding-top: 1rem;
    padding-bottom: 3rem;
  }
}